.email__verification__main{
    @apply flex flex-row w-full gap-10 items-center max-w-full;
    .email__verification__bg{
        background-image: url('../../assets/images/others/se.png');
        @apply bg-center bg-cover w-2/4 min-h-screen p-8;

        h1{
            @apply text-6xl text-white font-extrabold mt-28;
            span{
                @apply text-[#066DB8];
            }
        }
    }
    // email__verification__box
    .email__verification__box{
        @apply w-[583px] h-[530px] shadow-lg bg-white mt-4 rounded-md p-6;
        .email__logo{
            @apply flex justify-center;
        }
        h1{
            @apply text-center text-3xl font-bold;
        }
        .sub__hading{
            @apply flex justify-center pt-3;
            p span{
                @apply text-[#2068FF];
            }
        }
        h6{
            @apply text-center mt-2;
        }
        .small__text{
            @apply flex flex-col items-center gap-3 justify-center pt-2;
            small{
                @apply text-center;
            }
        }
        button{
            @apply bg-[#066DB8] rounded-md text-white w-full py-3 mt-8 hover:bg-[#066DB8] transition-all ease-linear duration-150 hover:text-white;
        }
       
    }
}