.otp__code__section__main{
    @apply flex flex-row w-full gap-10 max-w-full items-center;
    .otp__code__bg{
        background-image: url('../../assets/images/others/se.png');
        @apply bg-center bg-cover w-2/4 min-h-screen p-8;
        h1{
            @apply text-6xl text-white font-extrabold mt-28;
            span{
                @apply text-[#066DB8];
            }
        }
    }
    .otp__code__box{
        @apply w-[583px] h-[450px] shadow-lg mt-4 bg-white rounded-md pt-20 p-6 flex flex-col items-center gap-2;
        h1{
            @apply text-center text-3xl font-bold;
        }
        p{
            @apply text-center;
        }
        .otp__input{
            @apply flex flex-row items-center gap-4 mb-3;
            input{
                @apply border-b-2 border-solid border-gray-700 outline-none text-center text-sm;
                width: 42.18px;
height: 36.48px;
            }
        }
        button{
            @apply bg-[#066DB8] text-white rounded-md hover:bg-[#066DB8] transition-all w-full mt-8 ease-linear duration-150 hover:text-white;
                padding: 10px 40px;
        }
    }
}