.confirm__email__main{
    @apply flex flex-row w-full gap-10 items-center max-w-full;
}
.confirm__email__bg{
    background-image: url('../../assets/images/others/se.png');
    @apply bg-center bg-cover w-2/4 min-h-screen p-8;

    h1{
        @apply text-6xl text-white font-extrabold mt-28;
        span{
            @apply text-[#066DB8];
        }
    }
}
.confirm__email__box{
    @apply w-[583px] h-[530px] shadow-lg bg-white mt-4 rounded-md p-6 flex flex-col items-center gap-6 pt-32;
    h1{
        @apply text-center text-3xl font-bold;
    }
    button{
        @apply bg-[#066DB8] rounded-md text-white w-full py-3 hover:bg-[#066DB8] transition-all ease-linear duration-150 hover:text-white;
    }
   
}