.login__account__main{
    @apply flex flex-row w-full gap-10 max-w-full items-center;
    .login__account__bg{
        background-image: url('../../assets/images/others/se.png');
        @apply bg-center bg-cover w-2/4 min-h-screen p-8;
        h1{
            @apply text-6xl text-white font-extrabold mt-28;
            span{
                @apply text-[#066DB8];
            }
        }
    }
    .login__email__box{
        @apply w-[583px] h-[580px] shadow-lg mt-4 bg-white rounded-md p-6;
        h1{
            @apply text-center text-3xl font-bold;
        }
        .login__button__tabs{
            @apply flex items-center gap-3 mt-4 bg-[#F9F9F9] rounded-md p-2;
            button{
                @apply bg-[#E7E7E7] rounded-md hover:bg-[#066DB8] transition-all ease-linear duration-150 hover:text-white;
                padding: 7px 40px;
                &:nth-child(1) {
                    @apply bg-[#066DB8] text-white;
                }
            }
        }
        form {
            @apply mt-6;
            .form__input{
                @apply mb-3 xl:w-full;
                .form-label{
                    @apply inline-block font-bold mb-1 text-gray-700;
                }
                input{
                    @apply block w-full px-3 py-1.5 text-base font-medium text-gray-700 bg-white
                    border border-solid border-gray-300
                    rounded
                    transition
                    ease-in-out
                    m-0 mb-3
                    focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none;
                }
            }
            .rember__me__account{
                @apply flex justify-between items-center;
                .forgot__account__text{
                    @apply text-base text-[#1D77FF] hover:underline;
                }
            }
            button{
                @apply bg-[#066DB8] rounded-md w-full mt-4 text-white hover:bg-[#066DB8] transition-all ease-linear duration-150 hover:text-white;
                padding: 14px 40px;
            }
        }
        h6{
            @apply text-center mt-4;
            .sign__up{
                @apply font-bold;
            }
        }
    }
}