.signup__banner {
  @apply flex flex-row w-full gap-10 max-w-full;
}
.signup__banner .signup__bg {
  background-image: url("../../assets/images/others/se.png");
  @apply bg-center bg-cover w-2/4 min-h-screen p-8;
}
.signup__banner .signup__bg h1 {
  @apply text-6xl text-white font-extrabold mt-28;
}
.signup__banner .signup__bg h1 span {
  @apply text-[#066DB8];
}
.signup__banner .sign__up__box {
  @apply w-[583px] h-[auto] shadow-lg mt-4 rounded-md p-6;
}
.signup__banner .sign__up__box h1 {
  @apply text-center text-3xl font-bold;
}
.signup__banner .sign__up__box .button__tabs {
  @apply flex items-center gap-3 mt-4 bg-[#F9F9F9] rounded-md p-2;
}
.signup__banner .sign__up__box .button__tabs button {
  @apply bg-[#E7E7E7] rounded-md hover:bg-[#066DB8] transition-all ease-linear duration-150 hover:text-white;
  padding: 7px 40px;
}
.signup__banner .sign__up__box .button__tabs button:nth-child(1) {
  @apply bg-[#066DB8] text-white;
}/*# sourceMappingURL=Signup.css.map */