.vendor__page__header{
    @apply p-4 bg-white;
    .vendor__page__container{
        @apply container mx-auto;
        .logo__vendor__page{
            @apply flex flex-col;
            img{
                @apply w-52
            }
            span{
                @apply text-sm mt-6 ml-10;
            }
            .profile__details__links{
                @apply text-sm font-bold text-[#066DB8];
            }
        }
    }
}

// pro__vendor__sec
.pro__vendor__sec{
    @apply container mx-auto;
    .pro__hading{
        @apply text-2xl font-extrabold;
    }

    .pro__vendor__card{
        @apply w-full p-4 bg-white shadow-sm rounded-sm mt-2;
        .pro__vendor__row{
            @apply flex justify-between;
            .pro__vendor__row__first{
                @apply flex flex-row gap-4;
                .pro__vendor__jhon__certer__details{
                    @apply flex flex-col;
                    h2{
                        @apply font-bold text-xl;
                    }
                    h6{
                        @apply text-sm;
                    }
                    .reviews{
                        @apply flex gap-1 items-center;
                        small{
                            @apply text-sm;
                        }
                        span{
                            @apply text-sm underline text-[#949494];
                        }
                    }
                    .locations{
                        @apply flex items-center text-sm gap-2 mt-2;
                    }
                    .new__york{
                        @apply text-lg mt-2;
                    }
                    .desc{
                        @apply mt-4;
                    }
                    p{
                        @apply text-base mt-2;
                    }
                }
            }
        }
    }
}