.reset__password__main{
    @apply flex flex-row w-full gap-10 max-w-full items-center;
    .reset__password__bg{
        background-image: url('../../assets/images/others/se.png');
        @apply bg-center bg-cover w-2/4 min-h-screen p-8;
        h1{
            @apply text-6xl text-white font-extrabold mt-28;
            span{
                @apply text-[#066DB8];
            }
        }
    }
    .reset__password__box{
        @apply w-[583px] h-[450px] shadow-lg mt-4 bg-white rounded-md pt-20 p-6 flex flex-col items-center gap-2;
        h1{
            @apply text-center text-3xl font-bold;
        }
        p{
            @apply text-center text-[#9B9B9B];
        }
        .password__feilds{
            @apply w-[520px] relative;
        }
        .form-label{
            @apply inline-block mb-2 font-bold text-gray-700;
        }
        .form-control{
            @apply block
            w-full
            px-3
            py-1.5
            text-base
            font-normal
            text-gray-700
            bg-white
            border border-solid border-gray-300
            rounded
            transition
            ease-in-out
            m-0
            focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none
        }
        button{
            @apply bg-[#066DB8] w-full text-white py-3 rounded-md mt-4;
        }
        .hide{
            @apply absolute right-4 cursor-pointer top-11;
        }
    }
}